#body {
    background-color: steelblue;
    //flex弹性布局默认情况下就是水平排布
    //默认值为row
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    overflow: hidden;
    margin: 0;

    .empty {
        height: 150px;
        width: 150px;
        margin: 10px;
        border: solid 3px black;
        background: white;

    }

    .fill {
        background-image: url('https://source.unsplash.com/random/150x150');
        height: 145px;
        width: 145px;
        cursor: pointer;
    }

    .hold {
        border: solid 5px #ccc;
    }

    .hovered {
        background-color: #333;
        border-color: white;
        //表示显示为一系列短的方形虚线
        border-style: dashed;
    }
}


@media (max-width:800px) {
    #body {
        flex-direction: column;
    }
}